.channelRow {
    display: flex;
    align-items: center;
    width: 70%;
}

.channelRow___text {
    display: flex;
    flex-direction: column;
}

.channelRow___text p {
    color: #606060;
    font-size: small !important;
}

.channelRow___text h4 {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.channelRow___logo {
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
    border-radius: 50%;
}